import React from 'react';

const ConfirmEmail = ({ email }) => {
  return (
    <div className="confirmEmailContent">
      <div className="confirmEmailTitle">
        Prosz potwierd e podany email naley do Ciebie
      </div>
      <div className="confirmEmailBody">
        Na podany adres <strong>{email}</strong> wysalimy prob o
        potwierdzenie e jeste wacicielem tego e-mail. Jei jej nie
        otrzymae moesz wysla j ponownie.
      </div>
      <div className="confirmEmailButton">
        <button className="btn btn-green btn-capitalize btn-radius btn-login">
          Wylij ponownie
        </button>
      </div>
    </div>
  );
};

export default ConfirmEmail;
